import webpack from 'webpack';

export default function generateBuildImpl(option) {
  const useExternal = option.external === undefined ? true : !!option.external;
  
  return function webpackModifyImpl(webpackConfig) {
    if ( !useExternal ) {
      webpackConfig.resolve = webpackConfig.resolve || {};
      webpackConfig.resolve.alias = webpackConfig.resolve.alias || {};
      webpackConfig.resolve.alias['vue$'] = require.resolve('vue/dist/vue.common.js');
    }

    return webpackConfig;
  };
}
